.gpt3_cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius:1rem;
    background: var(--gradient-bar);
    color:white;
}
.gpt3_cta-content{
    display: flex;
    flex-direction:column;
    text-align: left;
}
.gpt3_cta-content p{
    font-family:var(--font-family);
    font-size:14px;
    line-height: 30px;
    font-weight: 500;
}
.gpt3_cta-content h3{
    font-family:var(--font-family);
    font-size:24px;
    line-height: 35px;
    font-weight: 800;
}
.gpt3_cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.gpt3_cta-btn button{
    background: #000;
    color:white;
    font-family: var(--font-family);
    font-weight: 700;
    font-size:18px;
    line-height: 30px;
    padding:.5rem 1rem;
    border-radius:2rem;
    border:none;
    outline:none;
    cursor: pointer;
    min-width:150px;
}

@media screen and (max-width:650px){
    .gpt3_cta{
        margin-right:4rem;
        flex-direction:column;
    }
    .gpt3_cta_content h3{
        width:100%;
    }
    .gpt3_cta-btn{
        margin:2rem 0 0;
    }
}
@media screen and (max-width:550px){
    .gpt3_cta{
        flex-direction:column;
        margin:4rem 2rem;
    }
    .gpt3_cta-content h3{
        font-style: 18px;
        line-height:32px;
    }
    .gpt3_cta-btn button{
       font-size:14px;
       line-height:28px;
    }
}