.gpt3_navbar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
  background: radial-gradient(
    circle at 3% 25%,
    rgba(3, 27, 52, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );
}
.gpt3_navbar-links {
  padding: 1rem 0;
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3_navbar-links_logo img {
  width: 42.56px;
}
.gpt3_navbar-links_container {
  display: flex;
  flex-direction: row;
}
.gpt3_navbar-sign {
  display: flex;
  align-items: center;
  right: 8rem;
  position: fixed;
}
.gpt3_navbar-links_container p,
.gpt3_navbar-sign p,
.gpt3_navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}
.gpt3_navbar-links_container p:hover {
  color: cyan;
  font-size: 20px;
  -webkit-transform: ease-in;
          transform: ease-in;
  transition: 0.2s;
  -webkit-transform: ease-out;
          transform: ease-out;
}
.gpt3_navbar-sign button,
.gpt3_navbar-menu_container button {
  padding: 0.5rem 1rem;
  line-height: 25px;
  left: 1153px;
  color: #fff;
  background: #ff4b20;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  top: 55px;
  border-radius: 5px;
  border: 0;
  outline: none;
  cursor: pointer;
}
.gpt3_navbar-menu {
  right: 6rem;
  display: none;
  position: fixed;
}

.gpt3_navbar-menu_container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  position: absolute;
  padding: 2rem;
  top: 40px;
  right: 0px;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}
.gpt3_navbar-menu_container p {
  margin: 1rem 0;
}
.gpt3_navbar-menu_container-links-sign {
  display: none;
}
@media screen and (max-width: 1050px) {
  .gpt3_navbar-links_container {
    display: none;
  }
  .gpt3_navbar-menu {
    display: flex;
  }
}
@media screen and (max-width: 700px) {
  .gpt3_navbar {
    padding: 2rem 4rem;
  }
  .gpt3_navbar-menu {
    right: 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3_navbar {
    padding: 2rem;
  }
  .gpt3_navbar-menu {
